.PlayListPreview-container {
    width: rem(202px);
    height: rem(380px);
    position: relative;
    top: rem(50px);
    cursor: pointer;
}

.PlayListPreview-container img {
    max-width: rem(200px);
    max-height: rem(200px);
    border-radius: 10%;
    position: relative;
}


.PlayListPreview-container div {
    justify-content: space-between;
    width: 100%;
    height: 100%;
    max-width: rem(146px);
    max-height: rem(73px) ;
    position: relative;
    top: rem(-51px);
    z-index: 1;
} 

.PlayListPreview-container h4 {
    font-family: rubik-light;
    font-size: rem(18px);
    line-height: rem(21.33px);
}

.PlayListPreview-container p {
    font-family: rubik;
    font-size: rem(14px);
    line-height: rem(16.59px);
    color: #646464;
    position: relative;
    top: rem(5px);
}


/*CAROUSEL*/

.carousel {
    position: relative;
    left: rem(75px);
}

.carousel>* {
    margin-left: 20px;
}

.arrows-container {
    position: relative;
}

.prev-arrow, .next-arrow {
    font-size: rem(20px);
    cursor: pointer;
}

.prev-arrow {
    position: absolute;
    left: rem(1195px);
    z-index: 1;
}

.next-arrow {
    position: absolute;
    right: rem(25px);
} 


.disabled {
    color: grey;
    pointer-events: none;
}

.playlist-title {
    font-family: rubik;
    font-weight: lighter;
    font-size: rem(28px);
    line-height: rem(33.18px);
    position: relative;
    max-width: rem(250px);
    left: rem(120px);
}

.div-carousel2 {
    position: relative;
    top: rem(60px);
}

.div-carousel3 {
    position: relative;
    top: rem(110px);
}



/*quick play*/

.quick-play-container {
    width: 68px;
    height: 68px;
    position: relative;
    bottom: rem(138px);
    left: rem(60px);
    visibility: hidden;
}

.visible-icon {
    width: 68px;
    height: 68px;
    position: relative;
    bottom: rem(138px);
    left: rem(60px);
    visibility: visible;
}

.dark-mode {
   background-color: #000000;
   opacity: 0.5;
}



/*quick playback*/

.quick-playback {
    border: 1px solid red;
    position: relative;
    right: 220px;
    overflow: visible;
    width: 100vw;
}